/* 
Checksum for the current mission, rally point or geofence plan, or for
the "combined" plan (a GCS can use these checksums to determine if it
has matching plans).         This message must be broadcast with the
appropriate checksum following any change to a mission, geofence or
rally point definition         (immediately after the MISSION_ACK that
completes the upload sequence).         It may also be requested using
MAV_CMD_REQUEST_MESSAGE, where param 2 indicates the plan type for
which the checksum is required.         The checksum must be
calculated on the autopilot, but may also be calculated by the GCS.
The checksum uses the same CRC32 algorithm as MAVLink FTP
(https://mavlink.io/en/services/ftp.html#crc32-implementation).
The checksum for a mission, geofence or rally point definition is run
over each item in the plan in seq order (excluding the home location
if present in the plan), and covers the following fields (in order):
frame, command, autocontinue, param1, param2, param3, param4, param5,
param6, param7.         The checksum for the whole plan
(MAV_MISSION_TYPE_ALL) is calculated using the same approach, running
over each sub-plan in the following order: mission, geofence then
rally point.

                mission_type              : Mission type. (uint8_t)
                checksum                  : CRC32 checksum of current plan for specified type. (uint32_t)

*/
mavlink20.messages.mission_checksum = function(mission_type, checksum) {

    this.format = '<IB';
    this.id = mavlink20.MAVLINK_MSG_ID_MISSION_CHECKSUM;
    this.order_map = [1, 0];
    this.crc_extra = 3;
    this.name = 'MISSION_CHECKSUM';

    this.fieldnames = ['mission_type', 'checksum'];


    this.set(arguments);

}
        mavlink20.messages.mission_checksum.prototype = new mavlink20.message();
mavlink20.messages.mission_checksum.prototype.pack = function(mav) {
    return mavlink20.message.prototype.pack.call(this, mav, this.crc_extra, jspack.Pack(this.format, [ this.checksum, this.mission_type]));
}

